<template>
  <v-container id="paper_t" fluid tag="section">
    <remote-js src="https://cdn.bootcss.com/mathjax/2.7.0/MathJax.js?config=TeX-AMS-MML_HTMLorMML"></remote-js>
    <template v-if="!flag115">
      <v-skeleton-loader v-bind="attrs" type="article, actions" height="500px"></v-skeleton-loader>
    </template>

    <template v-else>

      <v-row id="this_p">
        <v-card v-for="(item, index) in q_show" :key="item.description" class="mx-auto" width="750px">
          <v-card-text>

            <div class="text--primary" v-html="(page*5+index-4).toString() + '. ' + q_show[index].description"
              style="font-size: 16px;"> </div>
            <div v-html="q_show[index].choice[0]" style="margin-top: 10px; font-size: 15px;"></div>
            <div v-html="q_show[index].choice[1]" style="margin-top: 7px; font-size: 15px;"></div>
            <div v-html="q_show[index].choice[2]" style="margin-top: 7px; font-size: 15px;"></div>
            <div v-html="q_show[index].choice[3]" style="margin-top: 7px; font-size: 15px;"></div>

          </v-card-text>
          <v-card-actions>
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-btn icon @click="show_ans[page*5+index-5] = !show_ans[page*5+index-5]; key++" v-bind="attrs"
                  v-on="on">
                  <v-icon>{{ show_ans[page*5+index-5] ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
                </v-btn>
              </template>
              <span>查看答案</span>
            </v-tooltip>
            <v-spacer></v-spacer>
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-btn icon @click="$global.done=q_show[index].id;jump(0)" v-bind="attrs" v-on="on">
                  <v-icon>mdi-plus</v-icon>
                </v-btn>
              </template>
              <span>翻转</span>
            </v-tooltip>
          </v-card-actions>

          <v-expand-transition>
            <div v-show="show_ans[page*5+index-5]" :key="key">
              <v-divider></v-divider>
              <v-card-text v-html="'参考答案：' + q_show[index].answer">
                <!-- {{  }} -->
              </v-card-text>
            </div>
          </v-expand-transition>
        </v-card>
      </v-row>


      <v-pagination v-model="page" class="my-4" :length="5"></v-pagination>

    </template>
  </v-container>
</template>

<script>
  export default {
    name: 'paper_t',
    components: {
      'remote-js': {
        render(createElement) {
          return createElement('script', {
            attrs: {
              type: 'text/javascript',
              src: this.src,
              onload: this.onload
            }
          });
        },
        props: {
          src: {
            type: String,
            required: true
          },
        },
      },
    },
    data() {
      return {
        dones: [],
        paper: [],
        page: 1,
        page_num: '',
        selected: [],
        sheet: false,
        flag8: false,
        key: 0,
        flag114: 0,
        flag514: 0,
        loading: false,
        flag115: false,
        items: [{
          title: '翻转界面',
          path: './flippage',
        }, ],
      }

    },
    created() {
      var data = new Array()
      for (var i = 0; i < 100; i++) {
        data[i] = 0
      }
      this.show_ans = data
      this.$global2.load_config()
      this.$global2.load_css()
      this.get_wrongs();
    },
    computed: {
      q_show: function () {
        return this.dones.slice(this.page * 5 - 5, this.page * 5)
      },

    },
    methods: {
      jump(i) {
        this.$router.push({
          path: this.items[i].path
        })
      },
      get_wrongs: function () {
        var that = this;
        this.$axios({
          method: "get",
          url: "/api/get_wrongs/",
          params: {
            id: that.$global.userMsg.id,
          }
        }).then(function (response) {
          if (response.data.error_num == 0) {
            console.log("错题", response.data);
            that.dones = response.data.list;
            that.loading = false;
            setTimeout(() => {
              that.flag115 = true
              that.$nextTick(function () {
                that.$global2.MathQueue("this_p");
              })
            }, 100)
          } else {
            alert("获取失败！");
          }
        })
      },
    },

    watch: {
      page: {
        handler(newVal, oldVal) {
          var that = this;
          console.log("page = ", oldVal, newVal)
          that.$nextTick(function () {
            that.$global2.MathQueue("this_p");
          })
        },
        deep: true,
        immediate: true
      },
      flag114: {
        handler(newVal, oldVal) {
          var that = this;
          console.log("flag10 = ", oldVal, newVal)
          console.log(that.selected)
          that.$nextTick(function () {
            that.$global2.MathQueue("this_s");
          })
        },
        deep: true,
        immediate: true
      },
      flag515: {
        handler(newVal, oldVal) {
          var that = this;
          console.log("flag11 = ", oldVal, newVal)
          that.$nextTick(function () {
            that.$global2.MathQueue("this_s");
          })
        },
        deep: true,
        immediate: true
      },
    },
  }
</script>